import React, { useEffect, useState } from "react";
import "./SelectCharacter.css";
import { ethers } from "ethers";
import { CONTRACT_ADDRESS, transformCharacterData } from "../../constants";
import myEpicGame from "../../artifacts/contracts/MyEpicGame.sol/MyEpicGame.json";
import LoadingIndicator from "../LoadingIndicator";

/**
 * SelectCharacterã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆ
 * @param {*} param0 
 */
const SelectCharacter = ({ setCharacterNFT }) => {
    const [characters, setCharacters] = useState([]);
    const [gameContract, setGameContract] = useState(null);
    const [mintingCharacter, setMintingCharacter] = useState(false);

    /**
     * å‰¯ä½œç”¨ãƒ•ãƒƒã‚¯
     */
    useEffect(() => {
        const { ethereum } = window;
        if (ethereum) {
          const provider = new ethers.providers.Web3Provider(ethereum);
          const signer = provider.getSigner();
          const contract = new ethers.Contract(
            CONTRACT_ADDRESS,
            myEpicGame.abi,
            signer
          );
          // ã‚³ãƒ³ãƒˆãƒ©ã‚¯ãƒˆã®æƒ…å ±ã‚’ã‚»ãƒƒãƒˆã™ã‚‹ã€‚
          setGameContract(contract); 
        } else {
            console.log("Ethereum object not found");
        }
    }, []);

    /**
     * ã‚³ãƒ³ãƒˆãƒ©ã‚¯ãƒˆã®æƒ…å ±ãŒæ›´æ–°ã•ã‚ŒãŸã‚‰ã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼NFTã®ãƒ‡ãƒ¼ã‚¿ã‚’èª­ã¿è¾¼ã‚€
     */
    useEffect(() => {
        // ã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼NFTã‚’å–å¾—ã™ã‚‹ãƒ¡ã‚½ãƒƒãƒ‰
        const getCharacters = async () => {
            try {
              console.log("Getting contract characters to mint");
              // ãƒŸãƒ³ãƒˆå¯èƒ½ãªå…¨NFTã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼ã‚’å–å¾—ã™ã‚‹ã€‚
              const charactersTxn = await gameContract.getAllDefaultCharacters();
              console.log("charactersTxn:", charactersTxn);
        
              // ãƒ•ãƒ­ãƒ³ãƒˆã‚¨ãƒ³ãƒ‰å´ã§æ‰±ã„ã‚„ã™ã„ã‚ªãƒ–ã‚¸ã‚§ã‚¯ãƒˆã«å¤‰æ›ã™ã‚‹ã€‚
              const nftCharacters = charactersTxn.map((characterData) =>
                transformCharacterData(characterData)
              );
        
              // console.log("chracters:", nftCharacters);
              // ãƒŸãƒ³ãƒˆå¯èƒ½ãªã™ã¹ã¦ã®NFTã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼ã®çŠ¶æ…‹ã‚’è¨­å®šã—ã¾ã™ã€‚
              setCharacters(nftCharacters);
            } catch (error) {
              console.error("Something went wrong fetching characters:", error);
            }
          };

          // ã‚¤ãƒ™ãƒ³ãƒˆã‚’ã‚­ãƒ£ãƒƒãƒã—ãŸæ™‚ã«å®Ÿè¡Œã™ã‚‹ã€‚
          const onCharacterMint = async (sender, tokenId, characterIndex) => {
            console.log(`CharacterNFTMinted - sender: ${sender} tokenId: ${tokenId.toNumber()} characterIndex: ${characterIndex.toNumber()}`);
            
            if (gameContract) {
                // NFTã™ã‚‹ã€‚
                const characterNFT = await gameContract.checkIfUserHasNFT();
                console.log("CharacterNFT: ", characterNFT);
                // ãƒ‡ãƒ¼ã‚¿æ§‹é€ ã‚’å¤‰æ›ã™ã‚‹ã€‚
                setCharacterNFT(transformCharacterData(characterNFT));
                alert(
                    `NFT ã‚­ãƒ£ãƒ©ã‚¯ãƒ¼ãŒ Mint ã•ã‚Œã¾ã—ãŸ -- ãƒªãƒ³ã‚¯ã¯ã“ã¡ã‚‰ã§ã™: https://rinkeby.rarible.com/token/${
                        gameContract.address
                    }:${tokenId.toNumber()}?tab=details`
                );
            }
          };

          // ã‚³ãƒ³ãƒˆãƒ©ã‚¯ãƒˆãŒã‚»ãƒƒãƒˆã•ã‚Œã¦ã„ã‚‹å ´åˆã®ã¿å®Ÿè¡Œ
          if (gameContract) {
            getCharacters();
            // ãƒªã‚¹ãƒŠãƒ¼ã®è¨­å®š
            gameContract.on("CharacterNFTMinted", onCharacterMint);
          }
          return () => {
            // ã‚³ãƒ³ãƒãƒ¼ãƒãƒ³ãƒˆãŒãƒã‚¦ãƒ³ãƒˆã•ã‚ŒãŸã‚‰ã€ãƒªã‚¹ãƒŠãƒ¼ã‚’åœæ­¢ã™ã‚‹ã€‚
            if (gameContract) {
              gameContract.off("CharacterNFTMinted", onCharacterMint);
            }
          };
    }, [gameContract]);

    // ã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼NFTã®ãƒ‡ãƒ¼ã‚¿ã‚’è¡¨ç¤ºã™ã‚‹ãƒ¡ã‚½ãƒƒãƒ‰
    const renderChracters = () => 
        characters.map((character, index) => (
            <div className="character-item" key={character.name}>
              <div className="name-container">
                <p>{character.name}</p>
              </div>
              <img src={`https://cloudflare-ipfs.com/ipfs/${character.imageURI}`} />
              <button
                type="button"
                className="character-mint-button"
                onClick={mintCharacterNFTAction(index)}
              >{`Mint ${character.name}`}</button>
            </div>
        ));
     

    // ã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼NFTã‚’ç™ºè¡Œã™ã‚‹ãŸã‚ã®ãƒ¡ã‚½ãƒƒãƒ‰
    const mintCharacterNFTAction = (characterId) => async () => {
        try {
          if (gameContract) {
            setMintingCharacter(true);
            console.log("Minting character in progress...");
            // mintCharacterNFTãƒ¡ã‚½ãƒƒãƒ‰ã‚’å‘¼ã³å‡ºã™ã€‚
            const mintTxn = await gameContract.mintCharacterNFT(characterId);
            await mintTxn.wait();
            console.log("mintTxn:", mintTxn);
            setMintingCharacter(false);
          }
        } catch (error) {
          console.warn("MintCharacterAction Error:", error);
          setMintingCharacter(false);
        }
    };

    return (
        <div className="select-character-container">
            <h2>â¬ ä¸€ç·’ã«æˆ¦ã† NFT ã‚­ãƒ£ãƒ©ã‚¯ã‚¿ãƒ¼ã‚’é¸æŠ â¬</h2>
            {characters.length > 0 && (
                <div className="character-grid">
                    {renderChracters()}
                </div>
            )}
            {mintingCharacter && (
              <div className="loading">
                <div className="indicator">
                  <LoadingIndicator />
                  <p>Minting In Progress...</p>
                </div>
              </div>
            )}
        </div>
    );
};

export default SelectCharacter;